
export class Query {

    private data: any = {};

    public static make(): Query {
        return new Query();
    }

    public set(key: string, value: any): this {
        this.data[key] = value;
        return this;
    }

    public size(value: number = 10): this {
        return this.set('size', value);
    }

    public from(value: number = 0): this {
        return this.set('from', value);
    }

    public query(value: any): this {
        return this.set('query', value);
    }

    public conflictsProceed(): this {
        return this.set('conflicts', 'proceed');
    }

    public aggs(agg: any): this {

        let values = agg.toJSON();
        if (!this.data['aggs']) this.data['aggs'] = {};
        for (let i in values) this.data['aggs'][i] = values[i];

        return this;
    }

    public sort(value: any): this {
        return this.set('sort', value);
    }

    public toJSON() {
        return this.data;
    }

}